import React from "react";
import { Formik, Form, Field, ErrorMessage } from "formik";
import * as Yup from "yup";
import styles from "../../common/FormsControls/FormsControls.module.scss";
import n from "./ProfileInfo.module.scss";
import { ProfileType } from "../../../redux/types/types";

type PropsType = {
  profile: ProfileType;
  cancelEdit: () => void;
  onSubmit: (values: ProfileType) => Promise<void>;
};

const ProfileDataSchema = Yup.object().shape({
  fullName: Yup.string()
    .required("Required Field")
    .max(21, "Must be 21 characters or less"),
  lookingForAJobDescription: Yup.string()
    .required("Required Field")
    .max(200, "Must be 200 characters or less"),
  aboutMe: Yup.string()
    .required("Required Field")
    .max(200, "Must be 200 characters or less"),
});

const ProfileDataForm: React.FC<PropsType> = ({
  profile,
  cancelEdit,
  onSubmit,
}) => {
  return (
    <Formik
      initialValues={{
        fullName: profile.fullName || "",
        lookingForAJob: profile.lookingForAJob || false,
        lookingForAJobDescription: profile.lookingForAJobDescription || "",
        aboutMe: profile.aboutMe || "",
        contacts: profile.contacts || {},
        photos: profile.photos || {}
      }}
      validationSchema={ProfileDataSchema}
      onSubmit={onSubmit}
    >
      {({ errors, touched }) => (
        <Form className={n.MainEditedForm}>
          <div className={n.buttons}>
            <button
              type="button"
              className={n.cancelProfileBtn}
              onClick={cancelEdit}
            >
              Cancel
            </button>
            <button type="submit" className={n.editProfileBtn}>
              Save
            </button>
          </div>
          <div className={n.leftColFormData}>
            <div className={n.nameBlock}>
              <b className={n.nameTitle}>Full name:</b>
              <Field name="fullName" placeholder="Full name" className={styles.formControlInput} />
              <ErrorMessage name="fullName" component="span" className={styles.error} />
            </div>
            <div className={n.LookingJobBlock}>
              <b className={n.LookingJobTitle}>Looking for a job:</b>
              <Field name="lookingForAJob" type="checkbox" className={styles.formCheckbox} />
            </div>
            <div className={n.ProfessionalSkillsBlock}>
              <b className={n.ProfessionalSkillsTitle}>My professional skills:</b>
              <Field
                name="lookingForAJobDescription"
                as="textarea"
                placeholder="My professional skills"
                className={styles.formControlTextarea}
              />
              <ErrorMessage name="lookingForAJobDescription" component="span" className={styles.error} />
            </div>
            
            <div className={n.AboutMeBlock}>
              <b className={n.AboutMeTitle}>About me:</b>
              <Field
                name="aboutMe"
                as="textarea"
                placeholder="About me"
                className={styles.formControlTextarea}
              />
              <ErrorMessage name="aboutMe" component="span" className={styles.error} />
            </div>
          </div>
          <div className={n.Contacts}>
            <b className={n.ContactsTitle}>Contacts</b>
            <hr style={{ marginBlock: "10px" }} />
            {Object.keys(profile.contacts).map((key) => {
              if (key === "vk" || key === "mainLink") return null;
              return (
                <div key={key} className={n.contactBlock}>
                    <b className={n.contactTitle}>
                      {key} 
                    </b>:
                    <div>
                    <Field
                      name={`contacts.${key}`}
                      placeholder={key}
                      className={styles.formControlInput}
                    />
                    <ErrorMessage
                      name={`contacts.${key}`}
                      component="span"
                      className={styles.error}
                    />
                    </div>
                 
                </div>
              );
            })}
          </div>
        </Form>
      )}
    </Formik>
  );
};

export default ProfileDataForm;
